import { Component, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, FormGroupDirective, Validators } from '@angular/forms';

@Component({
  selector: 'app-todo-create-form',
  templateUrl: './todo-create-form.html',
  styles: [`
    .create-form__button {
      margin-left: 20px;
      width: 200px;
    }
  `],
})
export class TodoCreateFormComponent {
  @Output('create') createTodo = new EventEmitter();
  form: FormGroup;

  constructor(private fb: FormBuilder) { // <--- inject FormBuilder
    this.createForm();
  }

  createForm() {
    this.form = this.fb.group({
      name: ['', [Validators.required]],
    });
  }

  createNewTodo(FormDirective: FormGroupDirective) {
    this.createTodo.emit(this.form.get('name').value);
    this.form.reset();
    FormDirective.resetForm();
  }

}
