import { Component } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/internal/Observable';
import * as fromRoot from '../../../store/reducers';
import * as TodosActions from '../../../store/actions/todos';
import ITodo from '../../../models/Todo';

@Component({
  selector: 'app-local-dashboard',
  templateUrl: './local-dashboard.component.html',
  styles: [],
})
export class LocalDashboardComponent {
  todos$: Observable<ITodo[]>;

  constructor(private store: Store<fromRoot.State>) {
    this.todos$ = store.select('local');
    store.select(state => state).subscribe(state => console.log(state));
  }

  selectItem(id: number) {
    this.store.dispatch(new TodosActions.Select(id));
  }

  createItem(name: string) {
    const item: ITodo = {
      name,
      id: parseInt((Math.random() * 10000).toFixed(0), 10),
      selected: false,
    };
    this.store.dispatch(new TodosActions.AddOne(item));
  }

  deleteItem(id: number) {
    this.store.dispatch(new TodosActions.Delete(id));
  }
}
